.App {
  text-align: center;
}

.black-nav {
  background : black;
  width : 100%;
  display : flex;
  color : white;
  padding : 20px;
}
div {
  box-sizing : border-box
}
.list {
  position: relative;
  text-align : left;
  padding-left : 20px;
  border-bottom : 1px solid grey;
}
.chage-box{
  width: 45%;
  height: 50px;
  background: #61dafb;
  line-height: 50px;
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  border: none;
  margin: 5px 10px;
  border-radius: 5px;
}
.deleteBtn{
  width: 100px;
  height: 50px;
  background: #61dafb;
  position: absolute;
  right: 50px;
  top: calc(50% - 35px);
  color: #fff;
  border: none;
  border-radius: 10px;
}
.modal{
  margin-top : 20px;
  padding : 20px;
  background : #eee;
  text-align : left;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
